"use client";

import { useParams } from "next/navigation";
import categoryContent from "@/app/editorials/editorial/categoryData";

export default function EditorialCategory() {
  const params = useParams();
  const category = decodeURIComponent(params?.category || "");

  console.log("Category from URL:", category);
  console.log("Available categories:", Object.keys(categoryContent));

  const content = categoryContent[category] || "Контент не найден";

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h1 className="text-2xl font-bold">Редакция: {category}</h1>
      <p className="mt-4 whitespace-pre-line">{content}</p>
    </div>
  );
}
